 export function goodData(data) { //
     let typeList = ['', '', '', '', '']; //
     let goodCateList = data.cate_list.map(item => {
         return {
             name: item.title,
             id: item.id,
             children:item.children||[]
         }
     }); //
     let tmparr = data.goods.category_ids;
     let goodTypes = goodCateList.filter(item => {
         let len = tmparr.length;
         let result = false;
         for (let i = 0; i < len; i++) {
             if (item.id == tmparr[i]) {
                 result = true;
                 break;
             }
         }
         return result;
     }); //
     let formList = data.form_list;
     let statusList = ['', '', '', '', ''];

     return {
         info1: {
             goodType: {
                 label: '',
                 id: data.goods.id,
                 type: data.goods.type, ////1 2 3  4 5
                 value: typeList[data.goods.type - 1],
                 disabled: true, //,falsetrue
                 editable: 'input', //inputswitchimageimagelistselect

             },
             goodName: {
                 label: '',
                 id: data.goods.id,
                 value: data.goods.title,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
             },
             subTitle: {
                 label: '',
                 id: data.goods.id,
                 value: data.goods.sub_title,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
             },
             classification: {
                 label: '',
                 id: data.goods.id,
                 value: goodTypes.map(item => {
                     return item.name;
                 }).join(';'),
                 list: goodCateList, // 
                 goodTypes, //
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
             },
             mainImage: {
                 label: '',
                 id: '',
                 img: this.static_resources_domain + data.goods.thumb,
                 list: [{
                     img: this.static_resources_domain + data.goods.thumb,
                 }],
                 disabled: false, //
                 editable: 'image', //inputswitchimageimagelistselect
             },
             swiperList: {
                 label: '',
                 list: data.goods.thumbs.map(item => ({
                     img: this.static_resources_domain + item
                 })),
                 disabled: false, //
                 editable: 'imagelist', //inputswitchimageimagelistselect
             }
         },
         info2: {
             specification: {
                 label: '',
                 id: data.goods.has_option == 0 ? 'single' : 'multi', //
                 value: data.goods.has_option == 0 ? "" : "",
                 list: (data.options || []).map(item => {
                     return {
                         specif: {
                             label: '',
                             id: item.id,
                             value: item.title,
                             goodsId: item.goods_id,
                             disabled: true, //
                             editable: 'input', //inputswitchimageimagelistselect
                         },
                         price: {
                             label: '',
                             id: '',
                             value: item.price,
                             disabled: false, //
                             editable: 'input', //inputswitchimageimagelistselect
                         },
                         stock: {
                             label: '',
                             id: '',
                             value: item.stock,
                             disabled: false, //
                             editable: 'input', //inputswitchimageimagelistselect
                         },
                         code: {
                             label: '',
                             id: '',
                             value: item.goods_code,
                             disabled: false, //
                             editable: 'select', //inputswitchimageimagelistselect
                         }
                     }
                 }),
                 disabled: true, //
                 editable: 'input', //inputswitchimageimagelistselect
             },
             price: { //
                 label: '',
                 id: '',
                 value: data.goods.price,
                 disabled: false, //
                 editable: 'input', //inputswitchimageimagelistselect
             },
             delPrice: { //
                 label: '',
                 id: '',
                 value: data.goods.original_price,
                 disabled: false, //
                 editable: 'input', //inputswitchimageimagelistselect
             },
             stockNum: { //
                 label: '',
                 id: '',
                 value: data.goods.stock,
                 disabled: false, //
                 editable: 'input', //inputswitchimageimagelistselect
             },
             showStock: { //
                 label: '',
                 id: '',
                 value: data.goods.stock_hide == 0,
                 disabled: false, //
                 editable: 'switch', //inputswitchimageimagelistselect
             },
             soldNum: { //
                 label: '',
                 id: '',
                 value: data.goods.sales_count,
                 disabled: false, //
                 editable: 'input', //inputswitchimageimagelistselect
             },
             showSold: { //
                 label: '',
                 id: '',
                 value: data.goods.sales_hide == 0,
                 disabled: false, //
                 editable: 'switch', //inputswitchimageimagelistselect
             }
         },
         info3: {
             goodCode: {
                 label: '',
                 id: '',
                 value: data.goods.goods_code,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
             },
             autoDeliver: {
                 label: '',
                 id: '',
                 value: data.goods.auto_delivery == 1,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
                 needHide: false,
             },
             autoDeliverContent: {
                 label: '',
                 id: '',
                 value: data.goods.auto_delivery_content,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
                 needHide: false,
             },
             provideCost: {
                 label: '',
                 id: '',
                 value: data.goods.dispatch_price,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
                 needHide: false,
             },
             showProCost: {
                 label: '',
                 id: '',
                 value: data.goods.dispatch_hide == 0,
                 disabled: false, //
                 editable: 'switch', //inputswitchimageimagelistselect
             },
             joinCount: {
                 label: '',
                 id: '',
                 value: data.goods.is_discount == 1,
                 disabled: false, //
                 editable: 'switch', //inputswitchimageimagelistselect
             },
             goodForm: {
                 label: '',
                 id: data.goods.form_id,
                 value: formList.filter(item => {
                     let result = false;
                     for (let i = 0; i < formList.length; i++) {
                         if (item.id == data.goods.form_id) {
                             result = true;
                             break;
                         }
                     }
                     return result
                 }).map(item => {
                     return item.name
                 }).join(''),
                 formList,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
             }
         },
         info4: {
             status: {
                 label: '',
                 id: data.goods.status,
                 value: statusList[data.goods.status * 1 + 1],
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
             }
         },
     }
 }
